r\n}\r\n.upwarp-nodata{\r\n margin-bottom: 150px;\r\n font-size: 27px !important;\r\n}\r\n/*vant轻提示全局样式 */\r\n.van-toast {\r\n  min-width: 250px;\r\n  line-height: 50px;\r\n  font-size: 30px;\r\n  .van-toast__icon {\r\n    font-size: 100px;\r\n  }\r\n}\r\n/* 全局隐藏滚动条 */\r\n::-webkit-scrollbar{\r\n  display: none;\r\n}\r\n/* 烟包支付 */\r\n.user-payment{\r\n  width: 100%;\r\n  height: 100%;\r\n  overflow: hidden;\r\n  position: relative;\r\n  .title{\r\n    font-size: 23px;\r\n    color: $greyColor;\r\n  }\r\n  .text{\r\n    font-size: 25px;\r\n  }\r\n  .yin-zhang01,.yin-zhang02{\r\n    width: 90px;\r\n    height: 90px;\r\n    line-height: 90px;\r\n    text-align: center;\r\n    position: absolute;\r\n    background: url('./assets/img/yinzhang.png') no-repeat;\r\n    background-size: 100% 100%;\r\n    color: $redColor;\r\n    transform: rotate(-50deg);\r\n    font-size: 25px;\r\n  }\r\n  .yin-zhang01{\r\n    top: 10px;\r\n    left: 10px;\r\n  }\r\n  .yin-zhang02{\r\n    top: 10px;\r\n    left: 60px;\r\n  }\r\n  // .slide{\r\n  //   height: 100%;\r\n  //   overflow: hidden;\r\n  //   overflow: auto;\r\n  // }\r\n  .whole-package{\r\n    width: 95%;\r\n    height: 400px;\r\n    margin: 20px auto auto auto;\r\n    border-radius: 15px;\r\n    display: flex;\r\n    flex-wrap: nowrap;\r\n    align-items: center;\r\n    box-sizing: border-box;\r\n    padding: 0px 25px;\r\n    position: relative;\r\n    .package-bgk{\r\n      position: absolute;\r\n      top: 0px;\r\n      left: 0px;\r\n      z-index: -9999;\r\n      width: 100%;\r\n      height: 100%;\r\n    }\r\n    \r\n    .smoke-img{\r\n      width: 228px;\r\n      height: 350px;\r\n      margin-right: 30px;\r\n    }\r\n    .right-con{\r\n      width:61%;\r\n      \r\n    }\r\n    ul li{\r\n      width: 100%;\r\n      display: flex;\r\n      flex-wrap: nowrap;\r\n      align-items: center;\r\n      // margin-bottom: 15px;\r\n      overflow: hidden;\r\n      border-bottom: 2px solid $whiteColor;\r\n      box-sizing: border-box;\r\n      padding: 10px 0px;\r\n    }\r\n    ul li .smoke-icon{\r\n      width: 55px;\r\n      height: 55px;\r\n      margin-right: 12px;\r\n    }\r\n    ul li .right-content{\r\n      width: 85%;\r\n      line-height: 38px;\r\n    }\r\n    .right-content p{\r\n      width: 100%;\r\n      white-space: nowrap;\r\n      overflow: hidden;\r\n    }\r\n    \r\n  }\r\n  .boss-info{\r\n     width: 95%;\r\n     display: flex;\r\n     flex-wrap: nowrap;\r\n     align-items: center;\r\n     margin:20px auto auto auto;\r\n     border-radius: 15px;\r\n     box-sizing: border-box;\r\n     padding: 25px;\r\n     position: relative;\r\n     box-sizing: border-box;\r\n     .boss-bgk{\r\n      position: absolute;\r\n      top: 0;\r\n      left: 0;\r\n      width: 100%;\r\n      height: 100%;\r\n      z-index: -9999;\r\n     }\r\n     .img{\r\n       width: 228px;\r\n       height: 228px;\r\n       border: 1px solid $lightGrayColor;\r\n       border-radius: 15px;\r\n       margin-right: 30px;\r\n     }\r\n     .personal-info{\r\n       width: 65%;\r\n     }\r\n     .personal-info .p1{\r\n        margin-bottom: 50px;\r\n        text-overflow: ellipsis;\r\n     }\r\n     .personal-info p{\r\n       width: 95%;\r\n       display: flex;\r\n       flex-wrap: nowrap;\r\n       align-items: center;\r\n       white-space: nowrap;\r\n       overflow: hidden;\r\n     }\r\n     .left{\r\n       background-color: $ashesColor;\r\n       color: $whiteColor;\r\n       font-size: 25px;\r\n       display: flex;\r\n       align-items: center;\r\n       border-radius: 50px;\r\n       box-sizing: border-box;\r\n       padding: 0px 15px;\r\n       height: 50px;\r\n       margin-right: 15px;\r\n     }\r\n     .left i{\r\n       font-size: 35px;\r\n       margin-right: 5px;\r\n     }\r\n  }\r\n  .guanggao-img{\r\n    width: 95%;\r\n    height: 280px;\r\n    display: block;\r\n    margin: 20px auto auto auto;\r\n    border-radius: 15px;\r\n  }\r\n  .user-info{\r\n    width: 95%;\r\n    height: 150px;\r\n    margin: 20px auto auto auto;\r\n    position: relative;\r\n    .info-img{\r\n     position: absolute;\r\n     top: 0;\r\n     width: 100%;\r\n     height: 100%;\r\n     z-index: -9999;\r\n    }\r\n    ul{\r\n      width: 100%;\r\n      height: 150px;\r\n      display: flex;\r\n      flex-wrap: nowrap;\r\n      align-items: center;\r\n    }\r\n    .img{\r\n      width: 17%;\r\n    }\r\n    .margin{\r\n      margin: 0px 5px;\r\n    }\r\n    ul li{\r\n      width: 20%;\r\n      box-sizing: border-box;\r\n    }\r\n    .tx-nc{\r\n      margin-right: 5px;\r\n      color:$greyColor;\r\n    }\r\n    ul li img{\r\n      width: 110px;\r\n      height: 110px;\r\n      border-radius: 100px;\r\n    }\r\n    ul li p{\r\n      text-align: center;\r\n      white-space: nowrap;\r\n      overflow: hidden;\r\n    }\r\n    .wx-name{\r\n      text-overflow: ellipsis;\r\n    }\r\n    .text-left{\r\n      text-align: left;\r\n    }\r\n    .but{\r\n      height: 38px;\r\n      line-height: 38px;\r\n      background-color: $redColor;\r\n      color: $whiteColor;\r\n      border-radius: 50px;\r\n      text-align: center;\r\n      font-size: 25px;\r\n      box-sizing: border-box;\r\n      margin-top: 5px;\r\n    }\r\n  }\r\n  .weizhifu{\r\n    margin-bottom: 0px !important;\r\n  }\r\n  .tab{\r\n    // margin-bottom: 100px;\r\n    min-height: 100vw;\r\n    ul{\r\n      margin-top: 35px;\r\n      display: flex;\r\n      flex-wrap: wrap;\r\n    }\r\n    ul li{\r\n      width: 28%;\r\n      height: 300px;\r\n      margin: 0px 20px 30px 20px;\r\n      box-shadow: 0px 0px 10px 5px $lightGrayColor;\r\n      border-radius: 15px;\r\n      position: relative;\r\n      img{\r\n        width: 100%;\r\n        height: 180px;\r\n        border-radius: 15px 15px 0px 0px;\r\n      }\r\n    }\r\n    li p{\r\n      box-sizing: border-box;\r\n      padding: 0px 8px;\r\n    }\r\n    li .titel-name{\r\n      word-break: break-all;\r\n      text-overflow: ellipsis;\r\n      box-sizing: border-box;\r\n      paddi